.intro{
    padding-left: 24px;
    padding-right: 24px;
    height: 100vh;
    background-image: url("../../assets/images/illu/grille.svg");
    background-repeat: no-repeat;
    background-size: 610%;
    background-position-x: -200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.header{
    padding-left: 24px;
    padding-right: 24px;
    position: fixed;
    width: 100vw;
    box-sizing: border-box;
    top: 24px;
}

h1{
    font-family: 'owners-text', sans-serif;
    font-size: 76px;
    line-height: 130%;
    margin: 0;
}

.h1_nom{
    font-family: 'Pangaia';
    font-weight: 200;
    font-size: 101px;
    color: $c-element;
    display: block;
}

.description{
    margin-left: 24px;
    margin-right: 24px;

    &__para{
        font-size: 18px;
        font-family: 'owners-text', sans-serif;
        margin-bottom: 72px;
        line-height: 140%;

        &--big{
            font-size: 23px;
            padding-top: 144px;
            margin-top: 0;
        }
    }
}

.projet{
    margin: 0;
    padding: 0 18px;
    height: 750px;
    overflow: hidden;

    &__liste{
        list-style: none;
        margin: 0;
        padding: 0;
    }

    &__element{
        display: none;

        &--active{
            display: block;
            text-align: center;
        }
    }

    &__titre1{
        font-size: 32px;
        font-family: 'owners-text', sans-serif;
        margin: 0;
        padding-top: 198px;
    }

    &__titre2{
        font-size: 24px;
        font-family: 'Pangaia regular';
        font-weight: 200;
        margin: 36px 0 18px ;
    }
    
    &__para{
        font-size: 14px;
        font-family: 'owners-text', sans-serif;
        margin: 0 36px 36px;

    }

    &__illu{
        position: relative;
        transform: rotate(120deg);
        left: 50%;
        top: 850px;
        transform: translate(-50%, -60%);
        width: 1200px;

    }
}

@media (min-width: 450px){ //background size and font size
    .intro{
        background-size: 450%;
        background-position-x: -400px;
    }
    
    h1{
        font-size: 101px;
    }
    
    .h1_nom{
        font-size: 135px;
    }
}

@media (min-width: 550px){ //padding and margin left/right
    .header{
        padding-left: 64px;
        padding-right: 64px;
    }
    .intro{
        padding-left: 64px;
        padding-right: 64px;
    }
    .description{
        margin-left: 64px;
        margin-right: 64px;
    }
    .projet{
        padding: 0 64px;
    }
}

@media (min-width: 760px){ //tablet
    .header{
        padding-left: 66px;
        padding-right: 66px;
        top: 29px;
    }

    .intro{
        padding-left: 66px;
        padding-right: 66px;
        background-size: 350%;
        background-position-x: -200px;
    }
    
    h1{
        font-size: 123px;
    }
    
    .h1_nom{
        font-size: 165px;
    }
    
    .description{
        margin-left: 66px;
        margin-right: 66px;
    
        &__para{
            font-size: 22px;
            margin-bottom: 87px;
    
            &--big{
                font-size: 29px;
                padding-top: 246px;
            }
        }
    }
    
    .projet{
        padding: 0 66px;
        height: 750px;

    
        &__titre1{
            font-size: 39px;
            padding-top: 195px;
        }
    
        &__titre2{
            font-size: 29px;
            margin: 44px 0 22px;
        }
        
        &__para{
            font-size: 17px;
            margin: 0 39px 39px;
        }
    
        &__illu{
            left: 50%;
            top: 850px;
            transform: translate(-50%, -60%);
            width: 1200px;
    
        }
    }
}

@media (min-width: 950px){ //padding and margin left/right + background size
    .header{
        padding-left: 87px;
        padding-right: 87px;
    }

    .intro{
        padding-left: 87px;
        padding-right: 87px;
        background-size: 280%;
        background-position-x: -150px;
    }
    
    .description{
        margin-left: 87px;
        margin-right: 87px;
    }
    
    .projet{
        padding: 0 87px;
    }
    
    .h1_n{
        display: inline;
    }
}

@media (min-width: 1110px){ //ordinateur
    .header{
        padding-left: 86px;
        padding-right: 86px;
        top: 39px;
    }

    .intro{
        padding-left: 86px;
        padding-right: 86px;
        background-size: 200%;
        background-position-x: -250px;
    }
    
    h1{
        font-size: 135px;
    }
    
    .h1_nom{
        font-size: 179px;
    }
    
    .description{
        margin-left: 86px;
        margin-right: 86px;
    
        &__para{
            font-size: 24px;
            margin-bottom: 87px;
    
            &--big{
                font-size: 32px;
                padding-top: 246px;
            }
        }
    }
    
    .projet{
        padding: 0 66px;
        height: 750px;

    
        &__titre1{
            font-size: 43px;
            padding-top: 195px;
        }
    
        &__titre2{
            font-size: 32px;
            margin: 44px 0 22px;
        }
        
        &__para{
            font-size: 18px;
            margin: 0 39px 39px;
        }
    
        &__illu{
            left: 50%;
            top: 850px;
            transform: translate(-50%, -60%);
            width: 1200px;
    
        }
    }
}